import React, { PureComponent } from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  message
} from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
import { CsxValidateInput } from 'Form'

@connect(
  state => ({ ...state.outWarehouseListRefund }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '计划数',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '退货位出库数',
      width: 150,
      dataIndex: 'shippedQtyInput',
      key: 'shippedQtyInput',
      render: (text, record, index) => <section>
        <div>(可用: {record.thStoreLocationAvailableQty})</div>
        <CsxValidateInput defaultValue={record.planQty}  assign={true} style={{ width: '100px' }} reg={(record.unit && record.unit.toUpperCase()) || 'default'} error={record.shippedQtyInput ? record.shippedQtyInput.error : true} handleOnchange={(data) => {
          record.shippedQtyInput = data
          this.forceUpdate()
        }} />
      </section>
    }, {
      title: '库存短缺',
      width: 150,
      dataIndex: 'shortageQty',
      key: 'shortageQty'
    }, {
      title: '出库数',
      width: 150,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const { handleGetDetailDelivery, modalId } = this.props
    handleGetDetailDelivery({ urlParams: modalId })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowDeliveryModal: false })
  }

  // 出库 
  delivery = () => {
    const { handleStock, modalData } = this.props
    let reqData = {
      orderCode: modalData.orderCode,
      requestList: []
    }
    let error = false
    modalData.shippedOrderItemList.map((o, i) => {
      if (o.shippedQtyInput ? o.shippedQtyInput.error : true) {
        error = true
        return
      }
      reqData.requestList.push({
        id: o.id,
        orderCode: o.orderCode,
        planQty: o.planQty,
        productCode: o.productCode,
        shortageQty: o.shortageQty,
        shippedQty: o.shippedQtyInput.value
      })
    })
    if (error) {
      message.error('请输入正确的数据')
      return
    }
    let _this = this
    confirm({
      title: '温馨提示',
      content: '订单只能出库一次，确认出库？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleStock({ data: reqData }, () => {
          message.success('操作成功')
          const { handleGetList, searchParams, page, size } = _this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
          _this.handleCloseModal()
        })
      }
    })

  }
  render() {
    const { deliveryTableLoading, modalData } = this.props
    return (
      <Modal
        title="出库操作"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className='content'>
          <div className='header'>
            <Row type="flex" justify="start">
              <Col span={8} className='delBox'>
                <label>出库单：</label>
                <span>{modalData.orderCode || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label>业务类型：</label>
                <span>{modalData.businessTypeDesc || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label>配送客户：</label>
                <span>{modalData.customerName || ''}</span>
              </Col>
              {/* <Col span={8} className='delBox'>
                      <label>用户：</label>
                      <span>{modalData.receiveName || ''}</span>
                    </Col> */}
              <Col span={8} className='delBox'>
                <label>配送门店：</label>
                <span>{`${modalData.shopCode || ''}-${modalData.shopName || ''}`}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label>供应商：</label>
                <span>{`${modalData.supplierCode || ''}-${modalData.supplierName || ''}`}</span>
              </Col>
            </Row>
          </div>
          <div className='foot'>
            <div className='table-group' style={{ margin: 0 }}>
              <Table
                rowKey={(record, index) => index}
                loading={deliveryTableLoading}
                columns={this.columns}
                scroll={{ x: 1400, y: 400 }}
                dataSource={modalData.shippedOrderItemList}
                pagination={false}
              />
              <Row type="flex" justify="end" style={{ marginTop: '15px' }}>
                <Col>
                  <Button type="primary" onClick={this.delivery} >确认出库</Button>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </Modal>
    )
  }
}



